.specification-box {
    padding: 20px;
    box-sizing: border-box;
    background-color: #F6F6F6;
    display: flex;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
    .one-half {
        width: 50%;
        &:first-child {
            padding-right: 30px;
            border-right: 2px solid #e5e5e5;
            ul li {
                margin-bottom: 5px;
                strong {
                    font-weight: 600;
                }
            }
        }
        h4 {
            text-align: center;
            text-transform: uppercase;
            margin-bottom: 15px;
            color: #7c284e;
            margin-top: 0;
            font-weight: 600;
        }
        .button {
            display: flex;
            flex-direction: column;
            align-items: center;
            a {
                position: relative;
                border-radius: 30px;
                font-size: 13px;
                text-decoration: none;
                text-transform: uppercase;
                width: 140px;
                background-color:#7c284e;
                text-align: center;
                color: white;
                line-height: 32px;
                &:hover {
                    background-color: #4a001c;
                }
            }
            span {
                position: relative;
                color: #767676;
                line-height: 18px;
                white-space: pre;
                z-index: 1;
                margin-top: 3px;
                font-size: 13px;
            }
        }
    }
}
#irobot-roomba-e5 {
    .one-half {
        .button a{
            background-color: #d84343;
            &:hover {
                background-color: #982F2F;
            }
        }
    }
}
@media (max-width: 991px) {
    .specification-box {
        width: 100%;
        .one-half {
            &:first-child {
                border-right: none;
            }
            .button {
                a {
                    border-radius: 5px;
                }
            }
        }
    }
}
@media (max-width: 767px) {
    .specification-box {
        flex-direction: column;
        padding-left: 15px;
        .one-half {
            width: 100%;
            border: none;
            padding: 0;
            &:first-child {
                border: none;
                margin-bottom: 25px;
                ul {
                    padding-left: 20px;
                }
            }
        }
    }
}